<template>
  <div class="container">
    <el-tabs type="border-card">
      <el-tab-pane label="车队配费审批">
        <operation></operation>
      </el-tab-pane>
      <el-tab-pane label="车队模式下回滚12个月历史保单情况">
        <year-history></year-history>
      </el-tab-pane>
      <el-tab-pane label="评估月1日至评估日已审核协议情况">
        <month-info></month-info>
      </el-tab-pane>
      <el-tab-pane label="该代理协议的评估情况">
        <agent-agreement></agent-agreement>
      </el-tab-pane>
      <el-tab-pane label="车队模式历史数据与待申报数据合并后与预算对比分析">
        <budget-compare></budget-compare>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
const BaseTable = () => import('@/components/BaseTable')
const Operation = () => import('./Operation')
const YearHistory = () => import('../YearHistory')
const MonthInfo = () => import('../MonthInfo')
const AgentAgreement = () => import('../AgentAgreement')
const BudgetCompare = () => import('../BudgetCompare')

export default {
  name: 'Detail',
  components: {BaseTable, Operation, YearHistory, MonthInfo, AgentAgreement, BudgetCompare},
  data () {
    return {}
  }
}
</script>

<style scoped>
  .el-button {
    width: 120px;
    margin-right: 20px;
  }
</style>



